function extractPhoneNumbers(text) {
    // Fonction utilitaire pour compter les chiffres
    function countDigits(str) {
        return (str.match(/\d/g) || []).length;
    }

    const phonePatterns = [
        // Format international avec + ou 00
        /(?:\+|00)[1-9]\d{1,3}[- .]?\d(?:[- .]?\d){4,14}/g,
        
        // Formats standard (minimum 6 chiffres)
        /\d(?:[- .]?\d){5,14}/g
    ];

    function formatPhoneNumber(phone) {
        // V√©rifier d'abord si le num√©ro a au moins 6 chiffres
        if (countDigits(phone) < 6) {
            return null;
        }

        // Nettoyer l√©g√®rement le num√©ro tout en gardant le + et les espaces de formatage
        let cleaned = phone.trim()
            .replace(/[\(\)]/g, '') // Enlever les parenth√®ses
            .replace(/[-\.]/g, ' ') // Remplacer les points et tirets par des espaces
            .replace(/\s+/g, ' ');  // Normaliser les espaces

        // Si le num√©ro commence par 00, le remplacer par +
        if (cleaned.startsWith('00')) {
            cleaned = '+' + cleaned.slice(2);
        }

        return {
            formatted: cleaned,
            digitCount: countDigits(cleaned)
        };
    }

    let phones = new Map();

    for (let pattern of phonePatterns) {
        const matches = text.matchAll(pattern);
        for (const match of matches) {
            const phoneInfo = formatPhoneNumber(match[0]);
            if (phoneInfo && phoneInfo.digitCount >= 6) {
                if (!phones.has(phoneInfo.formatted)) {
                    phones.set(phoneInfo.formatted, phoneInfo);
                }
            }
        }
    }

    return Array.from(phones.values());
}

function populatePhoneSelect(phones) {
    const phoneSelect = document.getElementById('phone-select');
    const phoneInput = document.getElementById('phone-number');
    
    // Vider le select sauf l'option par d√©faut
    while (phoneSelect.options.length > 1) {
        phoneSelect.remove(1);
    }

    // Ajouter les num√©ros trouv√©s
    phones.forEach((phoneInfo, index) => {
        const option = document.createElement('option');
        option.value = phoneInfo.formatted;
        option.textContent = `Num√©ro ${index + 1}: ${phoneInfo.formatted}`;
        phoneSelect.appendChild(option);
    });

    // Si des num√©ros ont √©t√© trouv√©s, remplir l'input avec le premier
    if (phones.length > 0) {
        phoneInput.value = phones[0].formatted;
    }
}

