function loadEmailData() {
    log('Chargement des donn√©es de l\'email');
    const item = Office.context.mailbox.item;
    
    if (item) {
        setupPhoneInputs();

        // R√©initialiser le stockage des pi√®ces jointes
        if (item.attachments && item.attachments.length > 0) {
            log('Nombre de pi√®ces jointes trouv√©es:', item.attachments.length);
            
            // Afficher imm√©diatement les pi√®ces jointes
            const attachmentsInfo = item.attachments
                .filter(attachment => !attachment.isInline)
                .map(attachment => ({
                    name: attachment.name,
                    size: attachment.size,
                    contentType: attachment.contentType,
                    id: attachment.id
                }));
            
            displayAttachments(attachmentsInfo);
            
            // R√©initialiser le stockage des pi√®ces jointes
            window.attachmentsToUpload = [];
            
            // R√©cup√©rer le contenu des pi√®ces jointes
            attachmentsInfo.forEach(attachment => {
                Office.context.mailbox.item.getAttachmentContentAsync(attachment.id, (result) => {
                    if (result.status === Office.AsyncResultStatus.Succeeded) {
                        const base64Content = btoa(unescape(encodeURIComponent(result.value.content)));
                        const attachmentData = {
                            ...attachment,
                            content: base64Content
                        };
                        
                        window.attachmentsToUpload.push(attachmentData);
                        log('Pi√®ce jointe r√©cup√©r√©e:', {
                            name: attachment.name,
                            type: attachment.contentType,
                            size: attachment.size,
                            id: attachment.id
                        });
                    } else {
                        log('Erreur lors de la r√©cup√©ration de la pi√®ce jointe:', result.error);
                    }
                });
            });
        } else {
            log('Aucune pi√®ce jointe trouv√©e');
            displayAttachments([]);
        }

        // Reste du code existant...
        if (item.sender) {
            let senderEmail = item.sender.emailAddress;
            let senderName = item.sender.displayName;

            log('Donn√©es brutes de l\'exp√©diteur:', {
                rawEmail: senderEmail,
                rawName: senderName
            });

            senderEmail = cleanEmail(senderEmail);
            
            if (!senderName) {
                senderName = senderEmail.split('@')[0];
            }

            document.getElementById('sender-name').value = senderName;
            document.getElementById('sender-email').value = senderEmail;
            
            log('Informations de l\'exp√©diteur nettoy√©es:', {
                email: senderEmail,
                name: senderName
            });
        }

        // R√©cup√©rer le sujet
        const subject = item.subject || '';
        document.getElementById('email-subject').value = subject;
        log('Sujet charg√©:', subject);

        // R√©cup√©rer le corps du message
        item.body.getAsync(Office.CoercionType.Text, (result) => {
            if (result.status === Office.AsyncResultStatus.Succeeded) {
                const fullText = result.value;
                document.getElementById('email-body').value = fullText;

                // D√©tecter les num√©ros de t√©l√©phone dans le contenu
                const phones = extractPhoneNumbers(fullText);
                log('Num√©ros de t√©l√©phone d√©tect√©s:', phones);
                populatePhoneSelect(phones);

                // D√©tecter les emails dans le contenu
                const emails = extractEmails(fullText);
                log('Emails d√©tect√©s:', emails);
                populateEmailSelect(emails);
            } else {
                log('Erreur lors de la r√©cup√©ration du corps du message:', result.error);
            }
        });
    } else {
        log('Aucun √©l√©ment email trouv√©');
    }
}

function cleanEmail(email) {
    if (!email) return '';
    
    // Enlever le "<mailto:" et ">" si pr√©sent
    email = email.replace(/<mailto:/g, '').replace(/>/g, '');
    
    // Enlever les espaces
    email = email.trim();
    
    // Si l'email est r√©p√©t√©, prendre la premi√®re occurrence
    if (email.includes(' ')) {
        email = email.split(' ')[0];
    }
    
    return email;
}


function getDomainFromEmail(email) {
    return email.split('@')[1].toLowerCase();
}



function cleanName(name) {
    if (!name) return '';
    
    // Convertir en string si ce n'est pas d√©j√† le cas
    name = String(name);
    
    // Nettoyage plus agressif
    name = name
        // Enlever tous les ":" et espaces au d√©but
        .replace(/^[:\s]+/, '')
        // Enlever tous les ":" et espaces √† la fin
        .replace(/[:\s]+$/, '')
        // Enlever les guillemets
        .replace(/["']/g, '')
        // Enlever les caract√®res sp√©ciaux et les espaces multiples
        .replace(/[^\w\s√Ä-√ø-]/g, ' ')
        // Remplacer les espaces multiples par un seul espace
        .replace(/\s+/g, ' ')
        // Nettoyer les espaces au d√©but et √† la fin
        .trim();

    // Log du r√©sultat du nettoyage
    log('Nettoyage du nom:', {
        avant: String(arguments[0]),
        apr√®s: name
    });
    
    return name;
}


function parseEmailText(text) {
    const emails = [];
    
    // Diviser par les marqueurs "De :" ou "From :"
    const segments = text.split(/(?:\r?\n|\r)(?:De |From :)/).filter(Boolean);
    
    segments.forEach(segment => {
        const lines = segment.split(/\r?\n|\r/);
        let currentEmail = {
            name: '',
            from: '',
            subject: '',
            content: []
        };
        
        let isHeader = true;
        let contentStarted = false;
        
        for (let line of lines) {
            line = line.trim();
            
            if (!line) continue;
            
            // Traitement des en-t√™tes
            if (isHeader) {
                if (line.startsWith('From:') || line.includes('<')) {
                    const emailMatch = line.match(/<([^>]+@[^>]+)>/);
                    if (emailMatch) {
                        currentEmail.from = emailMatch[1];
                        currentEmail.name = line.split('<')[0].trim();
                    }
                } else if (line.startsWith('Subject:') || line.startsWith('Objet:')) {
                    currentEmail.subject = line.replace(/^(Subject:|Objet:)/, '').trim();
                    isHeader = false;
                } else if (line.match(/^[\w\s.]+@[\w\s.]+\.\w+$/)) {
                    currentEmail.from = line;
                } else if (!line.match(/^(√Ä|To|Date|Envoy√©|Sent):/)) {
                    contentStarted = true;
                    isHeader = false;
                }
            }
            
            // Contenu de l'email
            if (!isHeader && contentStarted && !line.match(/^(De|From|√Ä|To|Date|Envoy√©|Sent):/)) {
                currentEmail.content.push(line);
            }
        }
        
        if (currentEmail.content.length > 0) {
            currentEmail.content = currentEmail.content.join('\n').trim();
            emails.push(currentEmail);
        }
    });
    
    log('Segments d\'email analys√©s:', emails);
    return emails;
}



function formatEmailContent(emailBody) {
    if (!emailBody) return '';
    
    // Convertir les caract√®res encod√©s en HTML
    let cleanedContent = emailBody
        .replace(/=3D/g, '=')
        .replace(/=C3=A9/g, '√©')
        .replace(/=C3=A8/g, '√®')
        .replace(/=C3=A0/g, '√†')
        .replace(/=C3=AB/g, '√´')
        .replace(/=C3=BB/g, '√ª')
        .replace(/=C3=B4/g, '√¥')
        .replace(/=C3=AE/g, '√Æ')
        .replace(/=C3=A2/g, '√¢')
        .replace(/&nbsp;/g, ' ');

    // Pr√©server les sauts de ligne sp√©cifiques aux emails
    cleanedContent = cleanedContent
        // Remplacer <div><br></div> par un marqueur sp√©cial
        .replace(/<div><br><\/div>|<div><br\/><\/div>|<br>|<br\/>/g, '[[LINEBREAK]]')
        // G√©rer les divs avec du contenu
        .replace(/<div>(.*?)<\/div>/g, '$1\n')
        // Nettoyer les autres balises HTML
        .replace(/<[^>]+>/g, '')
        // Nettoyer les caract√®res sp√©ciaux restants
        .replace(/\r\n|\r|\n/g, '\n')
        .trim();

    // Convertir les marqueurs de saut de ligne en paragraphes HTML
    let paragraphs = cleanedContent
        .split('[[LINEBREAK]]')
        .map(p => p.trim())
        .filter(p => p !== '');

    // Reconstruire le HTML avec la structure appropri√©e
    cleanedContent = '<div style="white-space: pre-line; font-family: Arial, sans-serif;">';
    
    // Ajouter chaque paragraphe avec un espacement appropri√©
    paragraphs.forEach((paragraph, index) => {
        cleanedContent += `<p style="margin: 0; padding: 8px 0;">${paragraph}</p>`;
        // Ajouter un espace entre les paragraphes
        if (index < paragraphs.length - 1) {
            cleanedContent += '<div style="height: 12px;"></div>';
        }
    });

    cleanedContent += '</div>';

    // Ajouter les notes additionnelles si pr√©sentes
    if (cleanedContent.includes('Notes additionnelles')) {
        cleanedContent = cleanedContent.replace(
            /(Notes additionnelles.*?<\/p>)/,
            '<div style="margin: 30px 0;">' +
            '<hr style="border: none; border-top: 1px solid #cccccc;">' +
            '<h3>$1</h3>' +
            '<hr style="border: none; border-top: 1px solid #cccccc;">' +
            '</div>'
        );
    }

    return cleanedContent;
}

