function switchPage(pageId) {
    document.querySelectorAll('.page').forEach(page => {
        page.classList.remove('active');
    });
    document.getElementById(pageId).classList.add('active');

    document.querySelectorAll('.nav-button').forEach(button => {
        button.classList.remove('nav-active');
    });
    document.getElementById(pageId === 'app-body' ? 'nav-form' : 'nav-settings').classList.add('nav-active');
}

document.getElementById('save-to-crm').onclick = async function() {
    const button = this;
    const originalText = button.innerHTML;

    try {
        button.disabled = true;
        button.innerHTML = '<i class="ms-Icon ms-Icon--Sync"></i>Cr√©ation en cours...';

        const emailFrom = document.getElementById('sender-email').value.trim();
        const senderName = document.getElementById('sender-name').value.trim();
        const subject = document.getElementById('email-subject').value.trim();
        const emailBody = document.getElementById('email-body').value;
        const additionalNotes = document.getElementById('additional-notes').value.trim();

        if (!emailFrom || !subject) {
            throw new Error('L\'email de l\'exp√©diteur et le sujet sont requis');
        }

        const odoo = new OdooAPI();
        
        // Formatage du contenu pour pr√©server la mise en forme
        let description = '<div style="font-family: Arial, sans-serif;">';
        description += emailBody.split('\n').map(line => 
            `<p style="margin: 0 0 1em 0;">${line.trim()}</p>`
        ).join('');
        description += '</div>';

        if (additionalNotes) {
            description += '<div style="margin: 30px 0;">';
            description += '<hr style="border: none; border-top: 1px solid #cccccc;">';
            description += '<h3>Notes additionnelles :</h3>';
            description += '<hr style="border: none; border-top: 1px solid #cccccc;">';
            description += additionalNotes.split('\n').map(line => 
                `<p style="margin: 0 0 1em 0;">${line.trim()}</p>`
            ).join('');
            description += '</div>';
        }

        const result = await odoo.createLead(emailFrom, subject, description);

        if (result.success) {
            const settings = JSON.parse(localStorage.getItem('odooSettings'));
            const odooUrl = settings.odooUrl;

            showNotification(
                'Lead cr√©√© avec succ√®s dans Odoo',
                'success',
                {
                    leadId: result.leadId,
                    message: result.message,
                    logs: result.details,
                    odooUrl: odooUrl
                }
            );
        } else {
            throw new Error('√âchec de la cr√©ation du lead');
        }

    } catch (error) {
        log('Erreur compl√®te:', error);
        
        const errorDetails = {
            message: error.message,
            type: error.name,
            details: error.details || {},
            logs: error.logs || [],
            stack: error.stack
        };

        showNotification(
            'Erreur lors de la cr√©ation du lead',
            'error',
            errorDetails
        );

    } finally {
        button.disabled = false;
        button.innerHTML = originalText;
    }
};

function setupFormValidation() {
    const requiredFields = ['sender-email', 'email-subject'];
    const saveButton = document.getElementById('save-to-crm');

    function validateForm() {
        const isValid = requiredFields.every(fieldId => {
            const field = document.getElementById(fieldId);
            return field && field.value.trim() !== '';
        });

        saveButton.disabled = !isValid;
    }

    // Ajouter les √©couteurs d'√©v√©nements pour les champs requis
    requiredFields.forEach(fieldId => {
        const field = document.getElementById(fieldId);
        if (field) {
            field.addEventListener('input', validateForm);
            field.addEventListener('change', validateForm);
        }
    });

    // Validation initiale
    validateForm();
}

